import React, { useState, useEffect, useContext } from "react";
import { withRouter } from "react-router-dom";
import axios from "axios";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";
import { TaskContext } from "../context/taskContext/TaskContext";
import { getTasks } from "../context/taskContext/apiCalls";
import moment from "moment";
import { Form, Button, Row, Col } from "react-bootstrap";
import Dialog from "@mui/material/Dialog";
import Box from "@mui/material/Box";
import DialogContent from "@mui/material/DialogContent";
import CircularProgress from "@mui/material/CircularProgress";
export default function VenderDashbordBody() {
  let [projects, setProjects] = useState([]);
  let [projectsData, setProjectsData] = useState([]);
  let [subProjects, setSubProjects] = useState([]);
  let [filterTasks, setFilterTasks] = useState([]);
  let [items, setItems] = useState([]);
  let [allItems, setAllItems] = useState([]);
  let [data, setData] = useState([]);
  let [dailyData, setDailyData] = useState([]);
  const { tasks, dispatch } = useContext(TaskContext);
  let [form, setForm] = useState({});
  let [stat, setStat] = useState({});
  let [projectID, setProjectID] = useState(0);
  let [assignedProjectManager, setAssignedProjectManager] = useState("");
  let [companies, setCompanies] = useState([]);
  let [users, setUsers] = useState([]);
  let [lineChart1, setLineChart1] = useState([]);
  let [lineChart2, setLineChart2] = useState([]);
  let [openDialog, setOpenDialog] = useState(false);

  const axiosInstance = axios.create({
    baseURL: process.env.REACT_APP_BACKEND_URL,
  });

  let setField = (field, value) => {
    setForm({
      ...form,
      [field]: value,
    });
  };

  const changeProject = (e) => {
    setField("project", e.target.value);
    //         console.log(e.target.value)
    //  console.log(projectsData)
    // console.log(projectsData)
    let t = projectsData.filter((task) => task.project == e.target.value);
    setSubProjects([...new Set(t.map((ele) => ele.subProject))]);
    // console.log(t)
    for (var i = 0; i < projectsData.length; i++) {
      if (
        e.target.value == projectsData[i].project &&
        form.subProject == projectsData[i].subProject
      ) {
        // console.log(projectsData[i])
        setProjectID(projectsData[i].projectID);
        // console.log(projectsData[i].projectID)

        i = projectsData.length;
      } else if (i == projectsData.length - 1) {
        setProjectID("");
      }
    }
  };

  const changeSubProject = (e) => {
    // console.log(filterTasks)
    setField("subProject", e.target.value);
    // console.log(e.target.value)

    for (var i = 0; i < projectsData.length; i++) {
      if (
        form.project == projectsData[i].project &&
        e.target.value == projectsData[i].subProject
      ) {
        setAssignedProjectManager(projectsData[i].assignedProjectManager);
        setProjectID(projectsData[i].projectID);
        // console.log(projectsData[i])

        i = projectsData.length;
      } else if (i == projectsData.length - 1) {
        setProjectID("");
        setAssignedProjectManager("");
      }
    }
  };

  useEffect(() => {
    setOpenDialog(true);
    axiosInstance
      .get("projonline/users", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        setUsers(
          res.data.users.filter((obj) => obj["userCanAssignForTask"] === true)
        );
        setCompanies(
          res.data.companys.filter((obj) => obj.companyName !== "Mobitel")
        );
        return;
      })
      .catch((err) => {
        console.log(err);
      });
    axiosInstance
      .get("projonline/venderProject", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
          company_id: JSON.parse(localStorage.getItem("user")).company._id,
        },
      })
      .then((res) => {
        setForm({});

        setProjects([
          ...new Set(res.data.projTemplates.map((ele) => ele.project)),
        ]);
        setSubProjects([
          ...new Set(res.data.projTemplates.map((ele) => ele.subProject)),
        ]);
        setProjectsData(res.data.projTemplates);
      })
      .catch((err) => {
        console.log(err);
      });
    axiosInstance
      .get("projonline/gettasks", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        setItems(
          res.data.tasks.filter((obj) => obj["taskStatus"] != "Withdraw")
        );
        setAllItems(
          res.data.tasks.filter((obj) => obj["taskStatus"] != "Withdraw")
        );
        setStatFields(
          res.data.tasks.filter((obj) => obj["taskStatus"] != "Withdraw")
        );
        setOpenDialog(false);
        //setData(res.data.chartData)
        return;
      })
      .catch((err) => {
        console.log(err);
      });
    setLineChart1([
      {
        name: "No of tasks acceptance",
        dataKey: "taskAccept",
        stroke: "#dc3545",
        active: true,
      },
      {
        name: "No of task commencement",
        dataKey: "taskCommencement",
        stroke: "#4834d4",
        active: true,
      },
      {
        name: "No of tasks completion",
        dataKey: "taskComplete",
        stroke: "#17a2bb",
        active: true,
      },

      {
        name: "No of tasks commission",
        dataKey: "taskCommission",
        stroke: "#38a852",
        active: true,
      },
      {
        name: "No of task SAR",
        dataKey: "taskSAR",
        stroke: "#6c757d",
        active: true,
      },
      {
        name: "No of task PAT",
        dataKey: "taskPAT",
        stroke: "#677c14",
        active: true,
      },
      {
        name: "No of task onAir",
        dataKey: "taskOnAir",
        stroke: "#d1882b",
        active: true,
      },
    ]);

    setLineChart2([
      {
        name: "No of tasks acceptance",
        dataKey: "TaskAccept",
        stroke: "#dc3545",
        active: true,
      },
      {
        name: "No of task commencement",
        dataKey: "TaskCommenced",
        stroke: "#4834d4",
        active: true,
      },
      {
        name: "No of tasks completion",
        dataKey: "TaskComplete",
        stroke: "#17a2bb",
        active: true,
      },

      {
        name: "No of tasks commission",
        dataKey: "taskCommission",
        stroke: "#38a852",
        active: true,
      },
      {
        name: "No of task SAR",
        dataKey: "taskSAR",
        stroke: "#6c757d",
        active: true,
      },
      {
        name: "No of task PAT",
        dataKey: "taskPAT",
        stroke: "#677c14",
        active: true,
      },
      {
        name: "No of task onAir",
        dataKey: "taskOnAir",
        stroke: "#d1882b",
        active: true,
      },
    ]);

    // getChartData()
  }, []);

  useEffect(() => {
    const result = allItems.filter(
      (t) =>
        (form.project ? t.project === form.project : true) &&
        (form.subProject ? t.subProject === form.subProject : true) &&
        (form.subcon ? t.assignedSubcon?.companyName === form.subcon : true) &&
        (form.siteEngineer
          ? t.assignedMobitelOfficer?._id === form.siteEngineer
          : true)
    );
    setItems(result);
  }, [form]);

  useEffect(() => {
    getChartData();
    getDailyChartData();
    setStatFields(items);
  }, [items]);

  const legend1Onclick = (e) => {
    lineChart1.map((c) => {
      if (e.dataKey === c.dataKey) {
        var isActive = c.active;
        c.active = !isActive;
      }
    });
    setLineChart1(lineChart1);
    getChartData();
    legend2Onclick();
  };

  const legend2Onclick = (e) => {
    lineChart2.map((c) => {
      // console.log(e.dataKey, " ", c.dataKey);
      if (e.dataKey === c.dataKey) {
        var isActive = c.active;
        c.active = !isActive;
      }
    });
    setLineChart2(lineChart2);
    getDailyChartData();
  };

  const getChartData = () => {
    var chartData = [];
    var date = new Date();
    date.setDate(date.getDate() - 30);

    for (var i = 0; i <= 30; i++) {
      chartData[i] = {
        date: date.toISOString().split("T")[0],
        taskAccept: items
          .filter(
            (obj) =>
              typeof obj.taskHistory.find(
                (x) => x.taskStatus === "Task accepted"
              ) !== "undefined"
          )
          .filter(
            (obj) =>
              obj.taskHistory
                .find((x) => x.taskStatus === "Task accepted")
                .performedDate.split("T")[0] == date.toISOString().split("T")[0]
          ).length,
        taskCommencement: items
          .filter(
            (obj) =>
              typeof obj.taskHistory.find(
                (x) => x.taskStatus === "Task commenced"
              ) !== "undefined"
          )
          .filter(
            (obj) =>
              obj.taskHistory
                .find((x) => x.taskStatus === "Task commenced")
                .performedDate.split("T")[0] == date.toISOString().split("T")[0]
          ).length,

        taskComplete: items
          .filter(
            (obj) =>
              typeof obj.taskHistory.find(
                (x) => x.taskStatus === "Task completed"
              ) !== "undefined"
          )
          .filter(
            (obj) =>
              obj.taskHistory
                .find((x) => x.taskStatus === "Task completed")
                .performedDate.split("T")[0] == date.toISOString().split("T")[0]
          ).length,
        taskCommission: items
          .filter(
            (obj) =>
              typeof obj.taskHistory.find(
                (x) => x.comStatus === "Site Commissioned"
              ) !== "undefined"
          )
          .filter(
            (obj) =>
              obj.taskHistory
                .find((x) => x.comStatus === "Site Commissioned")
                .performedDate.split("T")[0] == date.toISOString().split("T")[0]
          ).length,
        taskSAR: items
          .filter(
            (obj) =>
              typeof obj.taskHistory.find((x) => x.sarStatus === "SAR Pass") !==
              "undefined"
          )
          .filter(
            (obj) =>
              obj.taskHistory
                .find((x) => x.sarStatus === "SAR Pass")
                .performedDate.split("T")[0] == date.toISOString().split("T")[0]
          ).length,
        taskPAT: items
          .filter(
            (obj) =>
              typeof obj.taskHistory.find((x) => x.patStatus === "PAT Pass") !==
              "undefined"
          )
          .filter(
            (obj) =>
              obj.taskHistory
                .find((x) => x.patStatus === "PAT Pass")
                .performedDate.split("T")[0] == date.toISOString().split("T")[0]
          ).length,
        taskOnAir: items
          .filter(
            (obj) =>
              typeof obj.taskHistory.find(
                (x) => x.onairStatus === "Site On-Air"
              ) !== "undefined"
          )
          .filter(
            (obj) =>
              obj.taskHistory
                .find((x) => x.onairStatus === "Site On-Air")
                .performedDate.split("T")[0] == date.toISOString().split("T")[0]
          ).length,
      };

      date.setDate(date.getDate() + 1);
    }
    // console.log(chartData);
    setData(chartData);
  };

  const getDailyChartData = () => {
    var chartData = [];
    var date = new Date();

    date.setDate(date.getDate() - 30);

    for (var i = 0; i <= 30; i++) {
      var pendingsub = items
        .filter(
          (obj) =>
            typeof obj.taskHistory.find(
              (x) => x.taskStatus === "Task handed over"
            ) !== "undefined"
        )
        .filter(
          (obj) =>
            obj.taskHistory
              .reverse()
              .find((x) => x.taskStatus === "Task handed over")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
        ).length;
      //console.log(items.filter((obj) => typeof obj.taskHistory.find(x => x.taskStatus === 'Task handed over') !== 'undefined').filter((obj) => obj.taskHistory.reverse().find(x => (x.taskStatus === 'Task handed over' || x.taskStatus==='Task rejected')).performedDate.split('T')[0] <= date.toISOString().split('T')[0]))
      var SubconAllocated = items.filter((obj) =>
        typeof obj.taskHistory.find(
          (x) => x.taskStatus === "Subcon allocated"
        ) !== "undefined"
          ? obj.taskHistory
              .find((x) => x.taskStatus === "Subcon allocated")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
          : false
      ).length;

      var pendingacc = items
        .filter(
          (obj) =>
            typeof obj.taskHistory.find(
              (x) => x.taskStatus === "Subcon allocated"
            ) !== "undefined"
        )
        .filter(
          (obj) =>
            obj.taskHistory
              .find((x) => x.taskStatus === "Subcon allocated")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
        ).length;
      var TaskAccept = items.filter((obj) =>
        typeof obj.taskHistory.find((x) => x.taskStatus === "Task accepted") !==
        "undefined"
          ? obj.taskHistory
              .find((x) => x.taskStatus === "Task accepted")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
          : false
      ).length;

      var pendingcomme = items
        .filter(
          (obj) =>
            typeof obj.taskHistory.find(
              (x) => x.taskStatus === "Task accepted"
            ) !== "undefined"
        )
        .filter(
          (obj) =>
            obj.taskHistory
              .find((x) => x.taskStatus === "Task accepted")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
        ).length;
      var TaskCommenced = items.filter((obj) =>
        typeof obj.taskHistory.find(
          (x) => x.taskStatus === "Task commenced"
        ) !== "undefined"
          ? obj.taskHistory
              .find((x) => x.taskStatus === "Task commenced")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
          : false
      ).length;

      var pendingcom = items
        .filter(
          (obj) =>
            typeof obj.taskHistory.find(
              (x) => x.taskStatus === "Task commenced"
            ) !== "undefined"
        )
        .filter(
          (obj) =>
            obj.taskHistory
              .find((x) => x.taskStatus === "Task commenced")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
        ).length;
      var TaskComplete = items.filter((obj) =>
        typeof obj.taskHistory.find(
          (x) => x.taskStatus === "Task completed"
        ) !== "undefined"
          ? obj.taskHistory
              .find((x) => x.taskStatus === "Task completed")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
          : false
      ).length;

      var TaskCommissioned = items.filter((obj) =>
        typeof obj.taskHistory.find(
          (x) => x.comStatus === "Site Commissioned"
        ) !== "undefined"
          ? obj.taskHistory
              .find((x) => x.comStatus === "Site Commissioned")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
          : false
      ).length;
      var TaskSARed = items.filter((obj) =>
        typeof obj.taskHistory.find((x) => x.sarStatus === "SAR Pass") !==
        "undefined"
          ? obj.taskHistory
              .find((x) => x.sarStatus === "SAR Pass")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
          : false
      ).length;
      var TaskPATed = items.filter((obj) =>
        typeof obj.taskHistory.find((x) => x.patStatus === "PAT Pass") !==
        "undefined"
          ? obj.taskHistory
              .find((x) => x.patStatus === "PAT Pass")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
          : false
      ).length;
      var TaskOnAired = items.filter((obj) =>
        typeof obj.taskHistory.find((x) => x.onairStatus === "Site On-Air") !==
        "undefined"
          ? obj.taskHistory
              .find((x) => x.onairStatus === "Site On-Air")
              .performedDate.split("T")[0] <= date.toISOString().split("T")[0]
          : false
      ).length;

      chartData[i] = {
        date: date.toISOString().split("T")[0],
        TaskAccept: pendingacc - TaskAccept,
        TaskCommenced: TaskAccept - TaskCommenced,
        TaskComplete: TaskCommenced - TaskComplete,
        taskCommission: TaskComplete - TaskCommissioned,
        taskSAR: TaskComplete - TaskSARed,
        taskPAT: TaskComplete - TaskPATed,
        taskOnAir: TaskComplete - TaskOnAired,
      };

      date.setDate(date.getDate() + 1);
    }

    // console.log(chartData);
    setDailyData(chartData);
  };

  const setStatFields = (items) => {
    let noOfTotalTasks = items.length;
    let noOfPendingSubconAllocations = items.filter(
      (obj) =>
        obj["taskStatus"] === "Task handed over" ||
        obj["taskStatus"] === "Task rejected"
    ).length;

    let noOfSubconAllocated = noOfTotalTasks - noOfPendingSubconAllocations;
    let noOfPendingTaskAcceptance = items.filter(
      (obj) => obj["taskStatus"] === "Subcon allocated"
    ).length;
    let NoOfAcceptedTasks = noOfSubconAllocated - noOfPendingTaskAcceptance;

    let noOfPendingTaskCommencement = items.filter(
      (obj) => obj["taskStatus"] === "Task accepted"
    ).length;

    let NoOfCommencedTasks = NoOfAcceptedTasks - noOfPendingTaskCommencement;

    let NoOfPendingTaskCompletion = items.filter(
      (obj) => obj["taskStatus"] === "Task commenced"
    ).length;
    let noOfWorkCompletion = NoOfCommencedTasks - NoOfPendingTaskCompletion;

    let NoOfCommission = items.filter(
      (obj) => obj["comStatus"] === "Site Commissioned"
    ).length;

    let NoOfPendingTaskCommission = noOfWorkCompletion - NoOfCommission;

    let NoOfSar = items.filter((obj) => obj["sarStatus"] === "SAR Pass").length;

    let NoOfPendingTaskSar = noOfWorkCompletion - NoOfSar;

    let NoOfPat = items.filter((obj) => obj["patStatus"] === "PAT Pass").length;

    let NoOfPendingTaskPat = noOfWorkCompletion - NoOfPat;

    let NoOfOnAir = items.filter(
      (obj) => obj["onairStatus"] === "Site On-Air"
    ).length;

    let NoOfPendingTaskOnAir = noOfWorkCompletion - NoOfOnAir;

    // console.log(NoOfPendingTaskCompletion, noOfPendingTaskAcceptance);

    const noOfCompleteAcceptance =
      noOfSubconAllocated - noOfPendingTaskAcceptance;
    const noOfCompleteCommencement =
      noOfCompleteAcceptance - noOfPendingTaskCommencement;
    const noOfCompleteCompletion =
      noOfCompleteCommencement - NoOfPendingTaskCompletion;
    const noOfCompleteCommission =
      noOfCompleteCompletion - NoOfPendingTaskCommission;
    const noOfCompleteSar = noOfCompleteCompletion - NoOfPendingTaskSar;
    const noOfCompletePat = noOfCompleteCompletion - NoOfPendingTaskPat;
    const noOfCompleteOnAir = noOfCompleteCompletion - NoOfPendingTaskOnAir;

    setStat({
      NoOfSubconAllocated: noOfSubconAllocated,

      NoOfPendingTaskAcceptance: noOfPendingTaskAcceptance,
      NoOfCompleteAcceptance: noOfCompleteAcceptance,

      NoOfPendingTaskCommencement: noOfPendingTaskCommencement,
      NoOfCompleteCommencement: noOfCompleteCommencement,

      NoOfPendingTaskCompletion: NoOfPendingTaskCompletion,
      NoOfCompleteCompletion: noOfCompleteCompletion,

      NoOfPendingCommission: NoOfPendingTaskCommission,
      NoOfCompleteCommission: noOfCompleteCommission,

      NoOfPendingSar: NoOfPendingTaskSar,
      NoOfCompleteSar: noOfCompleteSar,

      NoOfPendingPat: NoOfPendingTaskPat,
      NoOfCompletePat: noOfCompletePat,

      NoOfPendingOnAir: NoOfPendingTaskOnAir,
      NoOfCompleteOnAir: noOfCompleteOnAir,
    });
  };
  return (
    <div>
      {/* Content Wrapper. Contains page content */}
      <div className="content-wrapper">
        {/* Content Header (Page header) */}
        <div className="content-header">
          <Row className="mb-3">
            <Form.Group>
              <Form.Control
                onChange={changeProject}
                value={form.project}
                as="select"
                defaultValue="All Projects"
              >
                <option value="">All Projects</option>
                {projects.map((opt) => (
                  <option value={opt}>{opt}</option>
                ))}
              </Form.Control>
            </Form.Group>

            <Form.Group>
              <Form.Control
                onChange={changeSubProject}
                value={form.subProject}
                as="select"
                defaultValue="All Subproects"
              >
                <option value="">All Subprojects</option>
                {subProjects.map((opt) => (
                  <option value={opt}>{opt}</option>
                ))}
              </Form.Control>
            </Form.Group>
            {/* <Form.Group as={Col}>
              <Form.Control
                onChange={(e) => setField("subcon", e.target.value)}
                value={form.subcon}
                as="select"
                defaultValue="All Subcons"
              >
                <option value="">All Subcons</option>
                {companies.map((opt) => (
                  <option value={opt.companyName}>{opt.companyName}</option>
                ))}
              </Form.Control>
            </Form.Group> */}
            {/* <Form.Group as={Col}>
              <Form.Control
                onChange={(e) => setField("siteEngineer", e.target.value)}
                value={form.siteEngineer}
                as="select"
                defaultValue="All Site Engineers"
              >
                <option value="">All Site Engineers</option>
                {users.map((opt) => (
                  <option value={opt._id}>{opt.name}</option>
                ))}
              </Form.Control>
            </Form.Group> */}
          </Row>
        </div>
        {/* /.content-header */}
        {/* Main content */}
        <section className="content">
          <div className="container-fluid">
            {/* Small boxes (Stat box) */}
            <div className="row ">
              <div className="col-md-8 row d-flex align-items-end">
                <div className="col-12 col-sm-6 col-md-4">
                  <div
                    className="info-box"
                    style={{ backgroundColor: "#314757" }}
                  >
                    <span
                      className="info-box-icon elevation-1 text-light"
                      style={{ backgroundColor: "#dc3545" }}
                    >
                      <i className="fas fa-users" />
                    </span>
                    <div className="info-box-content">
                      <span className="info-box-text text-white">
                        Pending Task Acceptance
                      </span>
                      <span className="info-box-number text-white">
                        {stat.NoOfPendingTaskAcceptance}
                      </span>
                    </div>
                    {/* /.info-box-content */}
                  </div>

                  {/* /.info-box */}
                </div>

                <div className="col-12 col-sm-6 col-md-4">
                  <div
                    className="info-box"
                    style={{ backgroundColor: "#314757" }}
                  >
                    <span
                      className="info-box-icon elevation-1 text-light"
                      style={{ backgroundColor: "#4834d4" }}
                    >
                      <i className="fas fa-meh" />
                    </span>
                    <div className="info-box-content">
                      <span className="info-box-text text-white">
                        Pending Task commencement
                      </span>
                      <span className="info-box-number text-white">
                        {stat.NoOfPendingTaskCommencement}
                      </span>
                    </div>
                    {/* /.info-box-content */}
                  </div>

                  {/* /.info-box */}
                </div>

                <div className="col-12 col-sm-6 col-md-4">
                  <div
                    className="info-box"
                    style={{ backgroundColor: "#314757" }}
                  >
                    <span
                      className="info-box-icon elevation-1 text-light"
                      style={{ backgroundColor: "#17a2bb" }}
                    >
                      <i className="fas fa-people-carry" />
                    </span>
                    <div className="info-box-content">
                      <span className="info-box-text text-white">
                        Pending Task Completion
                      </span>
                      <span className="info-box-number text-white">
                        {stat.NoOfPendingTaskCompletion}
                      </span>
                    </div>
                    {/* /.info-box-content */}
                  </div>

                  {/* /.info-box */}
                </div>

                <div className="col-12 col-sm-6 col-md-4">
                  <div
                    className="info-box"
                    style={{ backgroundColor: "#314757" }}
                  >
                    <span
                      className="info-box-icon elevation-1 text-light"
                      style={{ backgroundColor: "#38a852" }}
                    >
                      <i className="fas fa-check-double" />
                    </span>
                    <div className="info-box-content">
                      <span className="info-box-text text-white">
                        Pending Task Commission
                      </span>
                      <span className="info-box-number text-white">
                        {stat.NoOfPendingCommission}
                      </span>
                    </div>
                    {/* /.info-box-content */}
                  </div>

                  {/* /.info-box */}
                </div>

                {/* /.col */}
                <div className="col-12 col-sm-6 col-md-4">
                  <div
                    className="info-box mb-3 "
                    style={{ backgroundColor: "#314757" }}
                  >
                    <span
                      className="info-box-icon elevation-1 text-light"
                      style={{ backgroundColor: "#6c757d" }}
                    >
                      <i className="fas fa-file-import" />
                    </span>
                    <div className="info-box-content">
                      <span className="info-box-text text-white">
                        Pending Task SAR
                      </span>
                      <span className="info-box-number text-white ">
                        {stat.NoOfPendingSar}
                      </span>
                    </div>
                    {/* /.info-box-content */}
                  </div>
                  {/* /.info-box */}
                </div>
                {/* /.col */}
                {/* fix for small devices only */}
                <div className="clearfix hidden-md-up" />
                <div className="col-12 col-sm-6 col-md-4">
                  <div
                    className="info-box mb-3 "
                    style={{ backgroundColor: "#314757" }}
                  >
                    <span
                      className="info-box-icon elevation-1 text-light"
                      style={{ backgroundColor: "#677c14" }}
                    >
                      <i className="fas fa-file-signature" />
                    </span>
                    <div className="info-box-content">
                      <span className="info-box-text text-white">
                        Pending Task PAT
                      </span>
                      <span className="info-box-number text-white">
                        {stat.NoOfPendingPat}
                      </span>
                    </div>
                    {/* /.info-box-content */}
                  </div>
                  {/* /.info-box */}
                </div>
                {/* /.col */}
                <div className="col-12 col-sm-6 col-md-4">
                  <div
                    className="info-box mb-3"
                    style={{ backgroundColor: "#314757" }}
                  >
                    <span
                      className="info-box-icon elevation-1 text-light"
                      style={{ backgroundColor: "#d1882b" }}
                    >
                      <i className="fas fa-clipboard-list" />
                    </span>
                    <div className="info-box-content">
                      <span className="info-box-text text-white">
                        Pending Task OnAir
                      </span>
                      <span className="info-box-number text-white">
                        {stat.NoOfPendingOnAir}
                      </span>
                    </div>
                    {/* /.info-box-content */}
                  </div>
                  {/* /.info-box */}
                </div>
                {/* /.col */}
              </div>

              <div class="col-md-4">
                <div class="progress-group">
                  No of accepted tasks
                  <span class="float-right">
                    <b> {stat.NoOfCompleteAcceptance}</b>/{" "}
                    {stat.NoOfSubconAllocated}
                  </span>
                  <div class="progress progress-sm">
                    <div
                      class="progress-bar"
                      style={{
                        backgroundColor: "#dc3545",
                        width: `calc(100% *${stat.NoOfCompleteAcceptance}/${stat.NoOfSubconAllocated})`,
                      }}
                    ></div>
                  </div>
                </div>
                <div class="progress-group">
                  No of work commencement
                  <span class="float-right">
                    <b> {stat.NoOfCompleteCommencement}</b>/{" "}
                    {stat.NoOfCompleteAcceptance}
                  </span>
                  <div class="progress progress-sm">
                    <div
                      class="progress-bar"
                      style={{
                        backgroundColor: "#4834d4",
                        width: `calc(100% *${stat.NoOfCompleteCommencement}/${stat.NoOfCompleteAcceptance})`,
                      }}
                    ></div>
                  </div>
                </div>
                <div class="progress-group">
                  No of work completion
                  <span class="float-right">
                    <b> {stat.NoOfCompleteCompletion}</b>/{" "}
                    {stat.NoOfCompleteCommencement}
                  </span>
                  <div class="progress progress-sm">
                    <div
                      class="progress-bar"
                      style={{
                        backgroundColor: "#17a2bb",
                        width: `calc(100% *${stat.NoOfCompleteCompletion}/${stat.NoOfCompleteCommencement})`,
                      }}
                    ></div>
                  </div>
                </div>
                <div class="progress-group">
                  No of Commission
                  <span class="float-right">
                    <b> {stat.NoOfCompleteCommission}</b>/{" "}
                    {stat.NoOfCompleteCompletion}
                  </span>
                  <div class="progress progress-sm">
                    <div
                      class="progress-bar"
                      style={{
                        backgroundColor: "#38a852",
                        width: `calc(100% *${stat.NoOfCompleteCommission}/${stat.NoOfCompleteCompletion})`,
                      }}
                    ></div>
                  </div>
                </div>
                <div class="progress-group">
                  No of Sar
                  <span class="float-right">
                    <b> {stat.NoOfCompleteSar}</b>/{" "}
                    {stat.NoOfCompleteCompletion}
                  </span>
                  <div class="progress progress-sm">
                    <div
                      class="progress-bar"
                      style={{
                        backgroundColor: "#6c757d",
                        width: `calc(100% *${stat.NoOfCompleteSar}/${stat.NoOfCompleteCompletion})`,
                      }}
                    ></div>
                  </div>
                </div>
                <div class="progress-group">
                  No of Pat
                  <span class="float-right">
                    <b> {stat.NoOfCompletePat}</b>/{" "}
                    {stat.NoOfCompleteCompletion}
                  </span>
                  <div class="progress progress-sm">
                    <div
                      class="progress-bar"
                      style={{
                        backgroundColor: "#677c14",
                        width: `calc(100% *${stat.NoOfCompletePat}/${stat.NoOfCompleteCompletion})`,
                      }}
                    ></div>
                  </div>
                </div>
                <div class="progress-group">
                  No of OnAir
                  <span class="float-right">
                    <b> {stat.NoOfCompleteOnAir}</b>/{" "}
                    {stat.NoOfCompleteCompletion}
                  </span>
                  <div class="progress progress-sm">
                    <div
                      class="progress-bar"
                      style={{
                        backgroundColor: "#d1882b",
                        width: `calc(100% *${stat.NoOfCompleteOnAir}/${stat.NoOfCompleteCompletion})`,
                      }}
                    ></div>
                  </div>
                </div>
              </div>
            </div>

            {/* /.row */}
            {/* Main row */}
            <div className="row">
              {/* Left col */}
              <section className="col-lg-12 connectedSortable">
                {/* Custom tabs (Charts with tabs)*/}
                <div className="card bg-light" style={{ height: "32rem" }}>
                  <div className="card-header">
                    <h3 className="card-title">
                      <i className="fas fa-chart-pie mr-1" />
                      Daily stats
                    </h3>
                  </div>
                  {/* /.card-header */}
                  <div className="card-body ">
                    <ResponsiveContainer>
                      <LineChart
                        data={data}
                        margin={{
                          top: 5,
                          right: 25,
                          left: 0,
                          bottom: 5,
                        }}
                      >
                        <CartesianGrid strokeDasharray="1 1" />
                        <XAxis dataKey="date" />
                        <YAxis />
                        <Tooltip />
                        <Legend onClick={legend1Onclick} />
                        {lineChart1.map((c) => (
                          <Line
                            type="monotone"
                            name={c.name}
                            dataKey={c.dataKey}
                            stroke={c.stroke}
                            strokeWidth={3}
                            hide={!c.active}
                          />
                        ))}
                      </LineChart>
                    </ResponsiveContainer>
                  </div>
                  {/* /.card-body */}
                </div>
                {/* /.card */}
              </section>
              {/* /.Left col */}
            </div>
            {/* /.row (main row) */}

            {/* /.row */}
            {/* Main row */}
            <div className="row">
              {/* Left col */}
              <section className="col-lg-12 connectedSortable">
                {/* Custom tabs (Charts with tabs)*/}
                <div className="card bg-light" style={{ height: "32rem" }}>
                  <div className="card-header">
                    <h3 className="card-title">
                      <i className="fas fa-chart-pie mr-1" />
                      Pending Stats
                    </h3>
                  </div>
                  {/* /.card-header */}
                  <div className="card-body ">
                    <ResponsiveContainer>
                      <LineChart
                        data={dailyData}
                        margin={{
                          top: 5,
                          right: 25,
                          left: 0,
                          bottom: 5,
                        }}
                      >
                        <CartesianGrid strokeDasharray="1 1" />
                        <XAxis dataKey="date" />
                        <YAxis />
                        <Tooltip />
                        <Legend onClick={legend2Onclick} />
                        {lineChart2.map((c) => (
                          <Line
                            type="monotone"
                            name={c.name}
                            dataKey={c.dataKey}
                            stroke={c.stroke}
                            strokeWidth={3}
                            hide={!c.active}
                          />
                        ))}
                      </LineChart>
                    </ResponsiveContainer>
                  </div>
                  {/* /.card-body */}
                </div>
                {/* /.card */}
              </section>
              {/* /.Left col */}
            </div>
            {/* /.row (main row) */}
          </div>
          {/* /.container-fluid */}
        </section>
        {/* /.content */}
      </div>
      <Dialog
        open={openDialog}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          <Box sx={{ display: "flex" }}>
            <CircularProgress />
          </Box>
        </DialogContent>
      </Dialog>
    </div>
  );
}
